.row{
	margin-left: 20px;
	color: white;
}
.row__posters{
	display: flex;
	overflow-y: hidden;
	overflow-x: scroll;
	padding:20px;
}

.row__posters::-webkit-scrollbar{
	display: none;
}

.row__poster{

	object-fit: contain;
	height: 100%;
	max-height: 125px;
	margin:0px 8px;
	transition: transform 450ms;

}

.row__poster:hover{
	transform: scale(1.08);
}

.row__posterLarge{
	max-height: 275px;
}

.row__posterLarge:hover{
	transform: scale(1.10);
}
